<div class="qkb-bot-ui" :class="uiClasses"><transition name="qkb-fadeUp"><div class="qkb-board" v-if="botActive"><BoardHeader :bot-title="optionsMain.botTitle" @close-bot="botToggle"></BoardHeader><BoardContent :bot-typing="botTyping" :main-data="messages"></BoardContent><BoardAction :input-disable="inputDisable" :input-placeholder="optionsMain.inputPlaceholder" :input-disable-placeholder="optionsMain.inputDisablePlaceholder" @msg-send="sendMessage"></BoardAction></div></transition><div class="qkb-bot-bubble"><button class="qkb-bubble-btn" @click="botToggle"><slot name="bubbleButton"><transition name="qkb-scaleUp"><BubbleIcon class="qkb-bubble-btn-icon" v-if="!botActive" key="1"></BubbleIcon><CloseIcon class="qkb-bubble-btn-icon qkb-bubble-btn-icon--close" v-else key="2"></CloseIcon></transition></slot></button></div><AppStyle :options="optionsMain"></AppStyle><div class="qkb-preload-image"><div class="qkb-msg-avatar__img" v-if="optionsMain.botAvatarImg"></div></div></div>