<div class="qkb-board-action" :class="actionClass"><div class="qkb-board-action__wrapper"><div class="qkb-board-action__msg-box"><input class="qkb-board-action__input" type="text" v-model="messageText" ref="qkbMessageInput" :disabled="inputDisable" :placeholder="inputPlaceholder" @keydown.enter="sendMessage"><div class="qkb-board-action__disable-text" v-if="inputDisablePlaceholder &amp;&amp; inputDisable"><span>{{ inputDisablePlaceholder }}</span></div></div><div class="qkb-board-action__extra"><slot name="actions"></slot><button class="qkb-action-item qkb-action-item--send" @click="sendMessage"><slot name="sendButton"><IconSend class="qkb-action-icon qkb-action-icon--send"></IconSend></slot></button></div></div></div>